 10;\n  // }\n}\n\n.sidebar {\n  color: $primaryTextColor;\n  font-family: $titleFont;\n  // width: 25%;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  font-size: 1.3rem;\n  padding: 40px;\n  text-align: left;\n}\n\n.sidebar p {\n  text-align: left;\n}\n\n.card {\n  border: none;\n}\n\n.legendItem {\n  display: flex;\n  color: #7b7b7b;\n  align-items: center;\n  align-content: flex-start;\n  margin: 0;\n  margin-bottom: 0;\n  font-size: 0.9rem;\n}\n\n.legendItem img {\n  width: 35px;\n}\n\n.legendItem p {\n  margin-left: 5px;\n}\n\n// #map {\n//   border: 1px solid black;\n//   width: 90%;\n//   height: 100vh;\n// }\n\n/* Note this is critical! Map doesn't appear or appears\nwonky without it. */\n.leaflet-container {\n  width: 100%;\n  height: 100vh;\n}\n\n.map-icon {\n  // border: 1px solid black;\n  width: 30px;\n  height: 30px;\n}\n\n.kp-popup .leaflet-popup-content-wrapper {\n  background-color: white;\n}\n\np.kp-popup-text {\n  margin-top: 0;\n  margin-bottom: 0;\n  font-size: 1.2rem;\n}\n// .kp-popup\n//   .leaflet-popup-content-wrapper\n//   .leaflet-popup-content\n\n.kp-popup-header {\n  margin-bottom: 3px;\n  font-size: 1.2rem;\n}\n\n.overlay {\n  // top: 70%;\n  // width: 100%;\n  // height: 10%;\n  background-color: rgba(92, 249, 249, 0.2);\n  z-index: 500;\n}\n\n.sliderContainer {\n  width: 90%;\n  margin-top: 50px;\n  height: 100px;\n}\n\n.MuiSlider-track {\n  height: $sliderTrackHeight !important;\n  color: $secondary-color !important;\n}\n\n.MuiSlider-rail {\n  height: $sliderTrackHeight !important;\n  color: $secondary-color !important;\n}\n\n.MuiSlider-mark {\n  height: $sliderTrackHeight !important;\n  color: $secondary-color !important;\n}\n\n.MuiSlider-thumb {\n  height: $sliderThumbSize !important;\n  width: $sliderThumbSize * 2 !important;\n  color: white;\n  border-radius: 5px !important;\n  border: 1px solid black;\n}\n\n// date picker input\n.MuiInputBase-input {\n  font-size: x-large !important;\n  font-weight: bold !important;\n}\n\ntable {\n  font-size: 1.1rem;\n  border-collapse: collapse;\n}\n\ntd,\nth {\n  border: 1px solid gray;\n  padding: 0.3rem;\n  font-size: 1.1rem;\n}\n\ntr:nth-child(even) {\n  background-color: #efefef;\n  // background-color: lightgray;\n}\n\n.poi-icon {\n  // overflow: hidden;\n  color: rgba(92, 249, 249, 0.612);\n  width: 20;\n  height: 20;\n}\n\n.currents-marker-container {\n  // padding: 1px;\n}\n\n.currents-marker-container path {\n  fill: $currentsMarkerColor;\n  // stroke: currentsMarkerColor;\n  // stroke-width: 1px;\n  // stroke-dasharray: 2, 2;\n  // stroke-linejoin: round;\n}\n\n.currents-marker-label {\n  color: $currentsMarkerColor;\n  font-weight: bold;\n  font-size: $markerLabelFontSize;\n  z-index: 300;\n}\n\n.stroke-text {\n  // color: white;\n  --stroke-color: black;\n  --stroke-width: 1.5px;\n  // color: rgb(255, 196, 0);\n  text-shadow: var(--stroke-width) 0 0 var(--stroke-color),\n    calc(var(--stroke-width) * -1) 0 0 var(--stroke-color),\n    0 var(--stroke-width) 0 var(--stroke-color),\n    0 calc(var(--stroke-width) * -1) 0 var(--stroke-color);\n}\n\n.time-slider-header {\n  font-size: 1.5rem;\n  font-weight: bold;\n  text-align: center;\n}\n\n// current marker container!\n.marker-container {\n  border: none;\n  background-color: none;\n}\n\n.tide-marker-container img {\n  background-color: rgba(92, 249, 249, 0.612);\n  border: 2px solid black;\n  width: 15px;\n  height: 15px;\n}\n\n.tide-marker-label {\n  font-weight: bold;\n  font-size: $markerLabelFontSize;\n  color: $tidesMarkerColor;\n  text-align: center;\n  z-index: 300;\n}\n\n.selected-marker {\n  // need another way to hilight these\n  // border: 2px solid black;\n}\n\n.selected-marker img {\n  background-color: cyan;\n  border: 5px solid black;\n  width: 25px;\n  height: 25px;\n}\n\n// .arrow-container {\n//   width: 50px;\n//   height: 50px;\n// }\n\n// svg * {\n//   transform-box: fill-box;\n// }\n\n// ======= LOADING ANIMATION ======= //\n.currents-marker-container.loader {\n  color: orange;\n}\n\n.tide-marker-container.loader {\n  color: black;\n}\n\n.loader {\n  font-size: 25px;\n  text-indent: -9999em;\n  overflow: hidden;\n  width: 1em;\n  height: 1em;\n  border-radius: 50%;\n  margin: 72px auto;\n  position: relative;\n  -webkit-transform: translateZ(0);\n  -ms-transform: translateZ(0);\n  transform: translateZ(0);\n  -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;\n  animation: load6 1.7s infinite ease, round 1.7s infinite ease;\n}\n@-webkit-keyframes load6 {\n  0% {\n    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,\n      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;\n  }\n  5%,\n  95% {\n    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,\n      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;\n  }\n  10%,\n  59% {\n    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em,\n      -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em,\n      -0.297em -0.775em 0 -0.477em;\n  }\n  20% {\n    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em,\n      -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em,\n      -0.749em -0.34em 0 -0.477em;\n  }\n  38% {\n    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em,\n      -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em,\n      -0.82em -0.09em 0 -0.477em;\n  }\n  100% {\n    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,\n      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;\n  }\n}\n@keyframes load6 {\n  0% {\n    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,\n      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;\n  }\n  5%,\n  95% {\n    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,\n      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;\n  }\n  10%,\n  59% {\n    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em,\n      -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em,\n      -0.297em -0.775em 0 -0.477em;\n  }\n  20% {\n    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em,\n      -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em,\n      -0.749em -0.34em 0 -0.477em;\n  }\n  38% {\n    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em,\n      -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em,\n      -0.82em -0.09em 0 -0.477em;\n  }\n  100% {\n    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em,\n      0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;\n  }\n}\n@-webkit-keyframes round {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n@keyframes round {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n","/*!\n * Bootstrap v5.1.3 (https://getbootstrap.com/)\n * Copyright 2011-2021 The Bootstrap Authors\n * Copyright 2011-2021 Twitter, Inc.\n * Licensed under MIT (https://github.com/twbs/bootstrap/blob/main/LICENSE)\n */\n\n// scss-docs-start import-stack\n// Configuration\n@import \"functions\";\n@import \"variables\";\n@import \"mixins\";\n@import \"utilities\";\n\n// Layout & components\n@import \"root\";\n@import \"reboot\";\n@import \"type\";\n@import \"images\";\n@import \"containers\";\n@import \"grid\";\n@import \"tables\";\n@import \"forms\";\n@import \"buttons\";\n@import \"transitions\";\n@import \"dropdown\";\n@import \"button-group\";\n@import \"nav\";\n@import \"navbar\";\n@import \"card\";\n@import \"accordion\";\n@import \"breadcrumb\";\n@import \"pagination\";\n@import \"badge\";\n@import \"alert\";\n@import \"progress\";\n@import \"list-group\";\n@import \"close\";\n@import \"toasts\";\n@import \"modal\";\n@import \"tooltip\";\n@import \"popover\";\n@import \"carousel\";\n@import \"spinners\";\n@import \"offcanvas\";\n@import \"placeholders\";\n\n// Helpers\n@import \"helpers\";\n\n// Utilities\n@import \"utilities/api\";\n// scss-docs-end import-stack\n",":root {\n  // Note: Custom variable values only support SassScript inside `#{}`.\n\n  // Colors\n  //\n  // Generate palettes for full colors, grays, and theme colors.\n\n  @each $color, $value in $colors {\n    --#{$variable-prefix}#{$color}: #{$value};\n  }\n\n  @each $color, $value in $grays {\n    --#{$variable-prefix}gray-#{$color}: #{$value};\n  }\n\n  @each $color, $value in $theme-colors {\n    --#{$variable-prefix}#{$color}: #{$value};\n  }\n\n  @each $color, $value in $theme-colors-rgb {\n    --#{$variable-prefix}#{$color}-rgb: #{$value};\n  }\n\n  --#{$variable-prefix}white-rgb: #{to-rgb($white)};\n  --#{$variable-prefix}black-rgb: #{to-rgb($black)};\n  --#{$variable-prefix}body-color-rgb: #{to-rgb($body-color)};\n  --#{$variable-prefix}body-bg-rgb: #{to-rgb($body-bg)};\n\n  // Fonts\n\n  // Note: Use `inspect` for lists so that quoted items keep the quotes.\n  // See https://github.com/sass/sass/issues/2383#issuecomment-336349172\n  --#{$variable-prefix}font-sans-serif: #{inspect($font-family-sans-serif)};\n  --#{$variable-prefix}font-monospace: #{inspect($font-family-monospace)};\n  --#{$variable-prefix}gradient: #{$gradient};\n\n  // Root and body\n  // stylelint-disable custom-property-empty-line-before\n  // scss-docs-start root-body-variables\n  @if $font-size-root != null {\n    --#{$variable-prefix}root-font-size: #{$font-size-root};\n  }\n  --#{$variable-prefix}body-font-family: #{$font-family-base};\n  --#{$variable-prefix}body-font-size: #{$font-size-base};\n  --#{$variable-prefix}body-font-weight: #{$font-weight-base};\n  --#{$variable-prefix}body-line-height: #{$line-height-base};\n  --#{$variable-prefix}body-color: #{$body-color};\n  @if $body-text-align != null {\n    --#{$variable-prefix}body-text-align: #{$body-text-align};\n  }\n  --#{$variable-prefix}body-bg: #{$body-bg};\n  // scss-docs-end root-body-variables\n  // stylelint-enable custom-property-empty-line-before\n}\n","// stylelint-disable declaration-no-important, selector-no-qualifying-type, property-no-vendor-prefix\n\n\n// Reboot\n//\n// Normalization of HTML elements, manually forked from Normalize.css to remove\n// styles targeting irrelevant browsers while applying new styles.\n//\n// Normalize is licensed MIT. https://github.com/necolas/normalize.css\n\n\n// Document\n//\n// Change from `box-sizing: content-box` so that `width` is not affected by `padding` or `border`.\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n\n// Root\n//\n// Ability to the value of the root font sizes, affecting the value of `rem`.\n// null by default, thus nothing is generated.\n\n:root {\n  @if $font-size-root != null {\n    font-size: var(--#{$variable-prefix}root-font-size);\n  }\n\n  @if $enable-smooth-scroll {\n    @media (prefers-reduced-motion: no-preference) {\n      scroll-behavior: smooth;\n    }\n  }\n}\n\n\n// Body\n//\n// 1. Remove the margin in all browsers.\n// 2. As a best practice, apply a default `background-color`.\n// 3. Prevent adjustments of font size after orientation changes in iOS.\n// 4. Change the default tap highlight to be completely transparent in iOS.\n\n// scss-docs-start reboot-body-rules\nbody {\n  margin: 0; // 1\n  font-family: var(--#{$variable-prefix}body-font-family);\n  @include font-size(var(--#{$variable-prefix}body-font-size));\n  font-weight: var(--#{$variable-prefix}body-font-weight);\n  line-height: var(--#{$variable-prefix}body-line-height);\n  color: var(--#{$variable-prefix}body-color);\n  text-align: var(--#{$variable-prefix}body-text-align);\n  background-color: var(--#{$variable-prefix}body-bg); // 2\n  -webkit-text-size-adjust: 100%; // 3\n  -webkit-tap-highlight-color: rgba($black, 0); // 4\n}\n// scss-docs-end reboot-body-rules\n\n\n// Content grouping\n//\n// 1. Reset Firefox's gray color\n// 2. Set correct height and prevent the `size` attribute to make the `hr` look like an